import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import authService from '../services/auth.service';

export const Login = () => {

    const navigate = useNavigate();
    const [isSubmitted, setIsSubmitted] = useState(false);

    const handleSubmit = async (data) => {
        setIsSubmitted(true)
        try {
          const result = await authService.login(data);
          if (true) {
            console.log("entered");
            navigate('/dashboard');
          }
        } catch (error) {
          console.log(error);
        }
        setIsSubmitted(false)
    };

    return (
        <div className="row">
            <div className="col-6 offset-3">
                <form >
                    <div className="mb-3">
                    <label htmlFor="inputEmail" className="form-label">Email address</label>
                    <input type="email" className="form-control" id="inputEmail" />
                    </div>
                    <div className="mb-3">
                    <label htmlFor="inputPassword" className="form-label">Password</label>
                    <input type="password" className="form-control" id="inputPassword" />
                    </div>
                    <button type="submit" onClick={handleSubmit} disabled={isSubmitted} className="btn btn-primary">Submit</button>
                </form>
            </div>
        </div>
    );
};

export const SignUp = () => {

    const navigate = useNavigate();
    const [isSubmitted, setIsSubmitted] = useState(false)

    const handleSubmit = async (data) => {
        setIsSubmitted(true)
        try {
          const result = await authService.register(data);
          console.log("Signin : ", result);
          if (result.data) {
            navigate('/');
          }
        } catch (error) {
            console.log(error);
        }
        setIsSubmitted(false)
    }

    return (
        <div className="row">
            <div className="col-6 offset-3">
                <form>
                    <div className="mb-3">
                        <label htmlFor="inputName" className="form-label">Name</label>
                        <input type="username" className="form-control" id="inputName" required/>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="inputEmail" className="form-label">Email address</label>
                        <input type="email" className="form-control" id="inputEmail" required/>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="inputPassword" className="form-label">Password</label>
                        <input type="password" className="form-control" id="inputPassword" />
                    </div>
                    <button type="submit" onClick={handleSubmit} className="btn btn-primary" disabled={isSubmitted}>Submit</button>
                </form>
            </div>
        </div>
    );
};
