import React, { useState } from 'react';

const Options = (props) => {
    return (
        <div className='mb-3'>
            <input type="text" placeholder={props.id} className='form-control'></input>
        </div>
    );
}

const CreatePoll = () => {

    const [isSubmitted, setIsSubmitted] = useState(false);
    const [optionsList, setOptionList] = useState([<Options id="Option1"/>, <Options id="Option2" />]);

    const handleAddOption = (event) => {
        setOptionList(prevlist => {
            return [...prevlist, <Options id={'Option' + ((prevlist.length) + 1)}/>]
        });
    }

    const handleSubmit = () => {
        setIsSubmitted(true);
        console.log("Poll submitted");
    }

    return (
        <div className="row">
            <div className="col-6 offset-3">
                <form >
                    <div className="mb-3">
                        <label htmlFor="question" className="form-label">Question</label>
                        <input type="text" className="form-control" id="question" />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="option1" className="form-label">Options</label>
                        {optionsList.map((x, i) => x)}
                    </div>
                    <button type="button" onClick={handleAddOption} className="btn btn-primary">Add option</button>
                    <button type="submit" onClick={handleSubmit} disabled={isSubmitted} className="offset-1 btn btn-primary">Submit</button>
                </form>
            </div>
        </div>
    );
};

export default CreatePoll;